"use client";

import React, { useContext, useState, useEffect } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { auth } from "@/firebase/firebase";
import { signOut } from "firebase/auth";
import { toast } from "react-toastify";
import { AuthContext } from "@/context/AuthContext";
import { FiLogOut, FiMoon, FiSun, FiBox, FiShoppingCart, FiRefreshCw } from "react-icons/fi";

export default function Dashboard() {
  const { user } = useContext(AuthContext);
  const router = useRouter();
  const [darkMode, setDarkMode] = useState(false);
  const [recentActivity, setRecentActivity] = useState([
    "Scanned item for update",
    "Sold item: Product A",
    "Checked inventory status",
  ]);

  useEffect(() => {
    document.documentElement.classList.toggle("dark", darkMode);
  }, [darkMode]);

  const handleLogout = async () => {
    try {
      await signOut(auth);
      toast.info("Logged out successfully!");
      router.replace("/login");
    } catch (error) {
      console.error("Logout error:", error);
      toast.error("Error logging out: " + error.message);
    }
  };

  return (
    <div className={`min-h-screen flex flex-col ${darkMode ? "bg-gray-900 text-white" : "bg-gray-50 text-gray-900"} p-4`}> 
      {/* Navbar */}
      <div className="flex justify-between items-center py-4 px-6 bg-opacity-75 backdrop-blur-md shadow-md rounded-md mb-6">
        <h1 className="text-3xl font-bold">Shopkeeper Dashboard</h1>
        <div className="flex items-center gap-4">
          {user && <span className="text-lg font-medium">{user.email}</span>}
          <button onClick={() => setDarkMode(!darkMode)} className="p-2 rounded-full bg-gray-300 dark:bg-gray-700">
            {darkMode ? <FiSun className="text-yellow-400" size={20} /> : <FiMoon className="text-gray-800" size={20} />}
          </button>
          <button onClick={handleLogout} className="p-2 rounded-full bg-red-500 hover:bg-red-600 text-white">
            <FiLogOut size={20} />
          </button>
        </div>
      </div>

      {/* Main Content */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 max-w-4xl mx-auto">
        {/* Scan for Update */}
        <Link href="/scan/update" className="p-6 bg-blue-500 text-white rounded-lg shadow-md flex flex-col items-center hover:bg-blue-600 transition">
          <FiRefreshCw size={40} />
          <span className="mt-2 text-lg">Scan for Update</span>
        </Link>

        {/* Scan for Sell */}
        <Link href="/scan/sell" className="p-6 bg-green-500 text-white rounded-lg shadow-md flex flex-col items-center hover:bg-green-600 transition">
          <FiShoppingCart size={40} />
          <span className="mt-2 text-lg">Scan for Sell</span>
        </Link>

        {/* View Inventory */}
        <Link href="/inventory" className="p-6 bg-purple-500 text-white rounded-lg shadow-md flex flex-col items-center hover:bg-purple-600 transition">
          <FiBox size={40} />
          <span className="mt-2 text-lg">View Inventory</span>
        </Link>
      </div>

      {/* Recent Activity Section */}
      <div className="mt-8 max-w-4xl mx-auto bg-white dark:bg-gray-800 p-6 rounded-lg shadow-md">
        <h2 className="text-2xl font-semibold mb-4">Recent Activity</h2>
        <ul className="space-y-2">
          {recentActivity.map((activity, index) => (
            <li key={index} className="p-2 bg-gray-100 dark:bg-gray-700 rounded-md">
              {activity}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}